<template>
  <div class="Characteristic">
    <div class="top">
      <a @click="goBack">
        <i></i>
      </a>
      <h3>{{cinemaName}}</h3>
    </div>
    <div class="address">
      <h3>{{cinemaName}}</h3>
      <p>{{addr}}</p>
    </div>
    <div class="image">
      <div class="heade">
        <img :src="imgUrl" alt>
      </div>
      <vue-scroll>
      <ul class="count child-dom" >
        <li v-for="(item, index) in imgTop" :key="index">
          <img :src="item.img" @click="imgClick" :class="{'active':index ==checkindex }" :id="index">
        </li>
      </ul>
      </vue-scroll>
      <div v-for="(detail, index) in details" :key="index" class="detail">
        <p>
          {{detail.nm}}
          <label>{{detail.sc}}分</label>
        </p>
        <span>{{detail.desc}}</span>
      </div>
    </div>

    <div class="times">
      <ul>
        <li v-for="(showdata, index) in dateShow" :key="index"  @click="toggle"
        :class="{'action':index ==checkindexs }">{{showdata.dateShow}}</li>
      </ul>
    </div>
    <ul class="showtime">
      <li v-for="(show, index) in shows" :key="index" @click="$router.push({ path: 'movetime', query: { plan: details,name:cinemaName}})">
        <h3>{{show.tm}} <label>{{show.lang}}{{show.tp}}</label></h3>
        <div v-for="(times, i) in Hour" :key="i">
          <p v-if="i == index">{{times}}散场 <label>{{show.th}}</label></p>
        </div>
        <h4>￥{{details[0].showCount}}</h4>
        <span>购票</span>
      </li>
    </ul>
    
  </div>
</template>
<style lang="less" scoped>
.Characteristic {
  .top {
    height: 50px;
    background-color: #e54847;
    display: flex;
    align-items: center;
    a {
      flex-grow: 1;
      i {
        margin-left: 20px;
        display: inline-block;
        line-height: 50px;
        width: 16px;
        height: 16px;
        border-bottom: 2px solid #ffffff;
        border-left: 2px solid #ffffff;
        transform: rotate(45deg);
      }
    }
    h3 {
      margin: 0;
      font-size: 18px;
      font-weight: normal;
      color: white;
      line-height: 50px;
      flex-grow: 1;
    }
  }
  .address {
    margin-left: 10px;
    width: 300px;
    overflow: hidden;
    h3 {
      font-size: 16px;
      margin-bottom: 5px;
    }
    
    p {
      width: 500px;
      margin: 0;
      color: #777;
      font-size: 14px;
    }
  }
  .image {
    margin-top: 20px;
    width: 100%;
    overflow: hidden;
    .heade {
      overflow: hidden;
      background-color: #333;
      width: 100%;
      height: 150px;
      position: absolute;
      z-index: -1;
      img {
        width: 100%;
        height: 150px;

        opacity: 0.55;

        filter: blur(1.2rem);
        overflow: hidden;
      }
    }
    .count {
      list-style: none;
      display: flex;
      margin-left: 148px;
      padding: 0;
      justify-content: space-between;
      li {
        transform-style: preserve-3d;
        margin-left: 10px;
        img {
          width: 74px;
          height: 109px;
        }
      }
      .active {
        transform: scale(1.1, 1.1) translateZ(4px);
        border: 2px solid white;
      }
    }
    .detail {
      text-align: center;
      margin-top: 20px;
      p {
        margin: 0;
        font-size: 15px;
        label {
          color: #ffb400;
          font-size: 16px;
        }
      }
      span {
        font-size: 13px;
        color: #777;
      }
    }
  }
  .times {
    width: 100%;
    overflow: hidden;
    ul {
      list-style: none;
      padding: 0;
      display: flex;
      margin-left: 10px;
      width: 1000px;
      justify-content: flex-start;
      border-bottom: 1px solid #e8e8e8;
      height: 40px;
      li {
        margin-right: 10px;
        font-size: 15px;
        color: #777;
      }
      .action{
        border-bottom: 2px solid #e54847;
        color:#e54847;
      }
    }
  }
  .showtime{
    list-style: none;
    padding:0;
    margin: 10px;
    li{
      border-bottom:1px solid #e8e8e8;
height:80px;
      position: relative;
      text-align: center;
      display: flex;
      justify-content: space-between;
      h3{
        margin: 0;
        height: 10px;
label{
          margin-left: 5px;
          font-size: 14px;
          color: #777;
        }
      }
      div{
        position: absolute;
        bottom: 15px;
      p{
        color: #777;
        font-size: 12px;
        label{
          margin-left: 5px;
        }
      }
    }
      
      h4{
color: #e54847;
font-size: 16px;
      }
      span{
display: inline-block;
width: 50px;
height: 25px;
background-color: #e54847;
color: white;
font-size: 14px;
border-radius: 5px;
line-height: 25px;
margin-top: 10px;
      }
    }
  }
}
</style>

<script>
export default {
  name: "Characteristic",
  data() {
    return {
      checkindex: 0,
      checkindexs: 0,
      ciemid: null,
      imgTop: null,
      shows: null,
      cinemaName: null,
      dateShow: null,
      addr: null,
      imgUrl: null,
      details: null,
      Hour: [],
      arr:[]
    };
  },
  created() {
    this.ciemid = this.$route.query.ciemid;
    this.$axios
      .get("http://api.kele8.cn/agent/http://m.maoyan.com/ajax/cinemaDetail?", {
        params: {
          cinemaId: this.ciemid
        }
      })
      .then(result => {
        this.addr = result.data.cinemaData.addr;
        this.cinemaName = result.data.showData.cinemaName;
        console.log(result.data.dealList)
        this.imgTop = result.data.showData.movies;
        this.dateShow = this.imgTop.splice(0, 1)[0].shows;
        this.shows = this.imgTop.splice(0, 1)[0].shows[0].plist;
        this.details = result.data.showData.movies.slice(0, 1);

        console.log(
          parseInt(this.details[0].desc) / 60,
          parseInt(this.details[0].desc) % 60
        );
        this.shows.forEach(elements => {
          let hour = +elements.tm.split(":")[0];
          let min = +elements.tm.split(":")[1];
          let broadcastHour = parseInt(parseInt(this.details[0].desc) / 60);
          let broadcastMin = parseInt(this.details[0].desc) % 60;
          let nawMin = broadcastMin + min;
          let newHour = broadcastHour + hour;


          if (nawMin > 60) {
            newHour = newHour + 1;
            nawMin = nawMin - 60;
          }
          if (nawMin < 10) {
            nawMin = "0" + nawMin;
          }
          if (newHour >= 24) {
            newHour = newHour - 24;
            if (newHour < 10) {
              newHour = "0" + newHour;
            } else {
              newHour = newHour;
            }
          }
          let time = newHour + ":" + nawMin;

          this.Hour.push(time);

        });
        result.data.showData.movies.forEach(element => {
          var imgUrl =
            "https://p1.meituan.net/128.180/movie/" + element.img.split("/")[5];
          element.img = imgUrl;
        });
        this.imgTop[1].img =
          "https://p0.meituan.net/128.180/moviemachine/f7d2ad70eb79d6d9b8a197713db9b8c41711752.jpg";
        this.imgUrl = result.data.showData.movies.slice(0, 1)[0].img;
      });
  },
  methods: {
    goBack: function() {
      this.$router.go(-1);
    },
    imgClick(e) {
      $(e.target).addClass("active").parent().siblings("li") .find("img").removeClass("active");
      let marginleft = $(e.target)
        .parents("ul")
        .css("margin-left");
      let index = $(e.target)
        .parent()
        .index();
      this.details = this.imgTop.slice(index, index + 1);
       this.dateShow = this.imgTop.slice(index, index + 1)[0].shows;
       console.log($(e.target)
        .parents("ul"))
        this.arr.unshift($(e.target).parent().index());
        console.log($(e.target).attr("id"))
        let num = this.arr.slice(0,2)
        // if(this.arr[$(e.target).attr("id")])
        console.log(this.arr,num)
       
        if(num[0] > num[1] || num.length == 1){
        $(e.target)
        .parents("ul")
        .css("margin-left", parseInt(marginleft) - 74 + "px");
        }
        else{
          console.log("dsfrsg")
          $(e.target)
        .parents("ul")
        .css("margin-left", (parseInt(marginleft) + 74) + "px");
        }
    },
    // go() {
    //   this.$router.push({ path: "movetime", query: { plan: "private" } });
    // },
  toggle (e) {
   
     $(e.target).addClass("action").siblings().removeClass("action");
  this.shows = this.imgTop.slice(0, 1)[0].shows[0].plist;
  }
  }
};
</script>
